<template>
  <label class="u-posRelative">
    <input
      type="text"
      class="
        u-color--brownish-grey
        u-bgc--real-white
        u-bc--white
        Input
      "
      :placeholder="placeholder"
      :type="type"
      v-on:keyup.13="handleOnEnterPress"
      required
    />
    <span
      class="
        u-color--warm-grey
        u-fontLighter
        u-posAbsolute
        u-hidden
        Placeholder"
      >{{placeholder}}</span>
  </label>
</template>

<script>
export default {
  props: ['placeholder', 'type', 'handleOnEnterPress'],
};
</script>

<style lang="stylus" scoped>
@import '../../styles/color-palette'
.Input
  height 64px
  width 100%
  border-width 1px
  border-style solid
  outline none
  padding 20px
  font-size 24px
  &::-webkit-input-placeholder
    color colors['pinkish-grey'];
  &:valid
    padding 25px 20px 15px
    & ~ .Placeholder
      display block !important

.Placeholder
  top 8px
  left 20px
  z-index 999
  font-size 12px
</style>



